#cancha{
  grid-column: 1 / 3;
  grid-row: 1;
  background-image: url('../images/field.jpg');
  background-size: 80%;
  background-repeat: no-repeat;
  height:600px;
  z-index: 0;
}

li{
  cursor: grab;
}

#cancha li {
  z-index: 99;
  color:white;
  font-weight: bold;
  width:150px;
}